import React from "react";
import "./Nav.css";

export default function nav() {
  const links = [
    {
      name: "Home",
      url: "/",
    },
    {
      name: "About",
      url: "#about",
    },
    {
      name: "Project",
      url: "#project",
    },
    {
      name: "Contact",
      url: "#contact",
    },
    {
      name: "Resume",
      url: "https://dev.kbatra.com/docs/Karan-Batra-Resume.pdf",
    },
  ];

  return (
    <nav>
      <ul className="nav__links flex">
        {links.map((link) => (
          <li className="link__items">
            <a className="link__item" href={`${link.url}`}>
              {link.name}
            </a>
          </li>
        ))}
      </ul>
    </nav>
  );
}
