import React, { useState, useEffect } from "react";
import "./Header.css";
import Logo from "../../Assets/img/primary-logo-black.png";
import Nav from "../Nav/Nav";
import ReactGA from "react-ga";

export default function Header() {
  ReactGA.initialize("UA-109323980-1");
  ReactGA.pageview(window.location.pathname + window.location.search);

  const [show, handleShow] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.scrollY > 100) {
        handleShow(true);
      } else handleShow(false);
    });
    return () => {
      window.removeEventListener("scroll");
    };
  }, []);
  return (
    <header className={`header ${show && "--scroll-nav"}`}>
      <div className="container flex">
        <h1 className="header__title">Karan Batra</h1>

        {/* <figure>
          <img className="logo" src={Logo} alt="Karan Batra Logo" />
        </figure> */}
        <Nav />
      </div>
    </header>
  );
}
