import React from "react";
import "./About.css";

export default function About() {
  return (
    <section id="about" className="about">
      <div className="container">
        <h1>About</h1>
        <p>
          I am a web developer located in Washington DC metropolitan area. I
          worked as an web developer for Sightline Media Group located in Tysons
          Corner in Virginia. Worked on redesigning and creating many websites
          using many languages such as HTML, Php, JavaScript and ReactJs. I am a
          student of George Mason University working on completing my degree in
          Information Technology. I have acquired an associate degree from
          Northern Virginia Community College.
        </p>
      </div>
    </section>
  );
}
