import React from "react";
import "./Jumbotron.css";
import Profile from "../../Assets/img/profile.JPG";
import BG from "../../Assets/img/bg.jpg";

export default function Jumbotron() {
  const links = [
    {
      name: "Github",
      url: "https://github.com/batra-karan",
    },
    {
      name: "LinkedIn",
      url: "https://www.linkedin.com/in/mrbatrakaran/",
    },
  ];
  return (
    <section
      className="jumbotron"
      style={{
        backgroundImage: "url(" + BG + ")",
        backgroundSize: "cover",
        height: "98vh",
        color: "#f5f5f5",
        backgroundAttachment: "fixed",
        backgroundPosition: "center",
      }}
    >
      <div className="container">
        <div className="jumbotron__content">
          <img
            className="jumbotron__profile-pic"
            src={Profile}
            alt="Karan Batra"
          />
          <h1>Karan Batra</h1>
          <p>Web Developer</p>
          <ul className="jumbotron__links">
            {links.map((link) => (
              <li className="link__items">
                <a className="link__item" href={`${link.url}`}>
                  {link.name}
                </a>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </section>
  );
}
