import React from "react";
import "./Project.css";

export default function Project() {
  const personal = [
    {
      name: "Netflix-Clone",
      url: "https://dev.kbatra.com/projects/netflix-clone/",
      description:
        "This is netflix clone created using Reactjs and The Movie Database.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/NetflixClone.jpg",
      github: "https://github.com/batra-karan/netflix-clone",
    },
    {
      name: "NewsGrid",
      url: "https://dev.kbatra.com/projects/news-grid/",
      description:
        "NewsGrid is a project to further my knowledge of CSS Grid and Flexbox.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/NewsGrid.jpg",
      github: "https://github.com/batra-karan/news-grid",
    },
    {
      name: "Edgeledger",
      url: "https://dev.kbatra.com/projects/edgeledger/",
      description: "Edgeledger is created using simple HTML and CSS.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/EdgeLedger.jpg",
      github: "https://github.com/batra-karan/edgeledger",
    },
    {
      name: "ACME Hotel",
      url: "https://dev.kbatra.com/projects/acme-hotel/",
      description: "ACME Hotel is created using simple HTML and CSS.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/ACMEHotel.png",
      github: "https://github.com/batra-karan/acme-hotel",
    },
  ];

  const professional = [
    {
      name: "C4ISRNET Conference",
      url: "https://c4isrconf.com/",
      description:
        "The 19th Annual C4ISR &amp; Networks Conference will bring together experts in cyber, C2/Comms, mobility, IT/networks, sensors, UAS/ISR, and GEOINT — Register today.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/C4Conf.png",
      github: "",
    },
    {
      name: "Cybercon",
      url: "https://cybercon.c4isrnet.com/",
      description:
        "C4ISRNET brings together military, intelligence community and homeland security leaders at CyberCon to discuss the latest in cyber defense and offense, and the future of cyberwar at home and abroad.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/Cybercon.jpg",
      github: "",
    },
    {
      name: "Defense News Conference",
      url: "https://conference.defensenews.com/",
      description:
        "Defense News Conference will bring together government, military and industry leaders to discuss the future of defense.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/DefConf.png",
      github: "",
    },
    {
      name: "HistoryNet",
      url: "https://www.historynet.com/",
      description:
        "HistoryNet.com contains daily features, photo galleries and over 5,000 articles originally published in our various magazines.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/HistoryNet.png",
      github: "",
    },
    {
      name: "MilitaryTimes",
      url: "https://www.militarytimes.com/",
      description:
        "Military Times is your trusted, independent voice for news about service members at home and deployed around the world",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/MilitaryTimes.png",
      github: "",
    },
    {
      name: "Sunset",
      url: "https://www.sunset.com/",
      description:
        "Sunset is the leading Western lifestyle brand. We deliver travel ideas, home design and gardening inspiration, and recipes that celebrate the West and its bounty.",
      cover: "https://raw.githubusercontent.com/batra-karan/batra-karan.github.io/main/images/Sunset.jpg",
      github: "",
    },
  ];

  return (
    <section id="project" className="project">
      <div className="container">
        <h1>Personal Projects</h1>
        <ul className="project__items">
          {personal.map((project) => (
            <li className="project__item">
              <div className="project__content">
                <figure>
                  <a
                    className="project__link"
                    href={`${project.url}`}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <img
                      className="project__cover"
                      src={project.cover}
                      alt={`${project.name}`}
                    />
                  </a>
                </figure>
                <div className="project__info">
                  <h3>{project.name}</h3>
                  <p className="project__description">{project.description}</p>
                  <a
                    className="project__github --active"
                    href={`${project.github}`}
                  >
                    Github
                  </a>
                </div>
              </div>
            </li>
          ))}
        </ul>
      </div>
      <div className="container">
        <h1>Professional Projects</h1>
        <ul className="project__items">
          {professional.map((project) => (
            <li className="project__item">
              <div className="project__content">
                <figure>
                  <a
                    className="project__link"
                    href={`${project.url}`}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <img
                      className="project__cover"
                      src={project.cover}
                      alt={`${project.name}`}
                    />
                  </a>
                </figure>
                <div className="project__info">
                  <h3>{project.name}</h3>
                  <p className="project__description">{project.description}</p>
                </div>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </section>
  );
}
